@import "../../static/styles/base.scss";
@import "../../static/styles/style.scss";

.item {
  position: relative;
  border-left: 6px solid $themeColor1;

  .time {
    position: relative;
    height: 58px;
    width: 190px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: $space;
    font-size: 28px;
    user-select: none;
    color: $textColor;
    @extend .trans;

    .dot {
      width: 30px;
      height: 30px;
      border-radius: 50%;
      position: absolute;
      left: -18px;
      top: 50%;
      transform: translate(0, -50%);
      background-color: $themeColor1;
      display: flex;
      justify-content: center;
      align-items: center;

      .dotIn {
        width: 14px;
        height: 14px;
        background-color: $hoverColor;
        border-radius: 50%;
        @extend .trans;
      }
    }
  }
  .time:hover .dotIn {
    width: 22px;
    height: 22px;
  }
  .content {
    list-style: none;
    margin: 0;
    padding: 0;
    margin: 0 16px;
    border-radius: 14px;
    padding: 10px $space;
    background-color: $themeColor1;
    @extend .trans;

    .timeLi {
      margin-bottom: 6px;
    }
    .timeLi:last-child {
      margin-bottom: 0;
    }
  }
  .content:hover {
    transform: scale(1.02);
  }
}

