.trans,.markdown a,.hover,.tagBase,.btnBase{transition:all .3s !important}.hover:hover,.tagBase:hover,.btnBase:hover{background-color:var(--hoverColor, rgb(98, 164, 218)) !important}.btnBase{position:absolute;width:70px;height:36px;display:flex;justify-content:center;align-items:center;user-select:none;bottom:0;border-radius:14px;font-size:18px;cursor:pointer;background-color:var(--themeColor1, rgb(22, 54, 51))}.tagBase{display:inline-block;padding:0 8px;margin:2px;height:30px;text-align:center;line-height:30px;font-size:18px;user-select:none;color:var(--textColor, rgb(255, 255, 255));border-radius:10px;cursor:pointer}.markdown h2,.markdown h3,.markdown h4,.markdown h5{margin-top:1rem;margin-bottom:.3rem;color:var(--textColor, rgb(255, 255, 255));font-weight:bold}.markdown h5{font-size:1.1rem}.markdown h4{font-size:1.2rem}.markdown h3{font-size:1.3rem}.markdown h2{font-size:1.4rem}.markdown h1{font-size:1.5rem}.markdown h2:before{content:"✒️"}.markdown h3:before{content:"✏️"}.markdown h4:before{content:"📌"}.markdown h5:before{content:"🏷️"}.markdown strong{color:var(--hoverColor, rgb(98, 164, 218))}.markdown p,.markdown a,.markdown ul{font-size:1rem;line-height:1.7rem !important;color:inherit;margin:0}.markdown ul{margin-top:.5rem;margin-bottom:.5rem}.markdown code{background-color:var(--inlineCode, rgb(29, 71, 67));padding:.1rem .25rem .11rem .25rem;font-family:"FiraCode" !important;font-size:.9rem !important;font-weight:normal !important;border-radius:8px;margin:0 2px}.markdown code span{font-family:"FiraCode"}.markdown blockquote{background-color:var(--themeColor1, rgb(22, 54, 51));padding:14px 40px;font-size:18px;border-radius:20px;border:2px var(--hoverColor, rgb(98, 164, 218)) dashed}.markdown blockquote p{margin:0}.markdown table{margin:20px auto;border:2px solid #787878;background-color:var(--themeColor1, rgb(22, 54, 51));border-spacing:0;text-align:center}.markdown table th,.markdown table td{padding:6px 14px;border:1px solid #787878}.markdown a{font-weight:700;text-decoration:underline !important;margin:0 4px;color:var(--hoverColor, rgb(98, 164, 218))}.markdown a:hover{color:red}.markdown hr{margin:20px 0}.markdown pre>div{background-color:var(--codeBg, rgb(37, 43, 48));border-radius:20px !important;padding:0;font-size:16px;font-family:"FiraCode","dengxian" !important;line-height:.1rem !important}